import React from "react";
import s from './Footer.module.css'
import styleContainer from '../../src/common/styles/Container.module.css'


export const Footer = (props) => {
    return (
        <div className={s.footerBlock}>
            <div className={`${styleContainer.container} ${s.footerContainer} `}>
                <h2 className={s.footerTitle}>Batskalevich Maria</h2>
                    <div className={s.footerSocialIcons}>
                        <img className={s.socialIcon} src="" alt="LinkedIn"/>
                        <img className={s.socialIcon} src="" alt="GitHub"/>
                        <img className={s.socialIcon} src="" alt="smth else"/>
                        <img className={s.socialIcon} src="" alt="smth else"/>
                    </div>
                    <span className={s.copyright}> 2022 All rights reserved</span>

            </div>
        </div>
    )
}
