import React from "react";
import s from '../hireMe/Hire.module.css'
import styleContainer from '../../src/common/styles/Container.module.css'


export const Hire = () => {
    return (
        <div className={s.hireBlock}>
            <div className={`${styleContainer.container} ${s.hireContainer} `}>
                <h2 className={s.title}>Considering options for remote work</h2>
                <a href="" className={s.hireBtn}>Hire me)))</a>

            </div>
        </div>
    )
}
