import React from "react";
import s from '../proect/Project.module.css'

export const Project = (props) => {
    return (
        <div className={s.project}>
            <div className={s.projectPicture}>
                <a className={s.btn}>See my project</a>
            </div>
            <h3>{props.title}</h3>
            <span className={s.description}>{props.description}</span>
        </div>
    )
}
