import React from "react";
import s from '../skills/Skills.module.css'
import styleContainer from '../../src/common/styles/Container.module.css'
import {Skill} from "./skill/Skill";


export const Skills = () => {
    return (
        <div className={s.skillsBlock}>
            <div className={`${styleContainer.container} ${s.skillsContainer}`}>
                <h2 className={s.title}>Skills</h2>
                <div className={s.skills}>
                    <Skill title={'JS'} description={'Lorem ipsum dolor sit'}/>
                    <Skill title={'CSS'}  description={'Lorem hbdxf njdfjmn dfjmn dfjmn fjndf dfjndfj dfhjdfjd ipsum dolor sit'}/>
                    <Skill title={'React'}  description={'Lorem ipsum dolor sit'}/>
                </div>
            </div>
        </div>
    )
}
